import styled from "@emotion/styled";
import React, { useCallback, useEffect, useState } from "react";
import { theme } from "src/Style/theme";
import Banner from "./Banner";
import Header from "./Header";
import TabItems from "./TabItems";

const Wrapper = styled.section`
  position: relative;
  @media screen and (max-width: ${({ theme }) => theme.device.mobile}) {
  }

  @media screen and (max-width: 640px) {
    font-size: 14px;
  }
`;

interface HeadersProps {
  bannerContent: string;
  bannerSrc: string;
}

const Headers: React.VFC<HeadersProps> = ({ bannerContent, bannerSrc }) => {
  const [bannerHide, setBannerHide] = useState(false);
  const [params, setParms] = useState<"" | "">("");
  const [searchClick, setSearchClick] = useState(false);

  const onClickBannerCloseBtn = useCallback(() => {
    setBannerHide(true);
  }, []);

  useEffect(() => {
    if (window.location.pathname === "/store") {
      setParms("");
    } else {
      setParms("");
    }
  }, []);

  const onSearhToggle = useCallback((boolean: boolean) => {
    setSearchClick(boolean);
  }, []);

  return (
    <Wrapper>
      <Banner
        hide={bannerHide}
        onClickBanner={() => {}}
        fontColor="white"
        backgroundColor={theme.color.orange}
        onClickCloseButton={onClickBannerCloseBtn}
      >
        {bannerContent}
      </Banner>
      <Header
        isClicked={searchClick}
        onSearchClick={(bool: boolean) => onSearhToggle(bool)}
        current={params}
        titles={[
          { content: "", href: "/" },
          { content: "", href: "/store" },
        ]}
        infos={[
          { content: " ", href: "/support" },
          { content: "", href: "/lecture" },
          { content: "", href: "/login" },
        ]}
      />
      <TabItems
        mainNavs={[
          { content: "1 ", href: "/event/signup" },
          { content: "", href: "/event" },
          { content: "", href: "/stduy" },
          { content: " ", href: "/class/new" },
          { content: "", href: "/class/ready" },
        ]}
        subNavs={[
          { content: "", href: "/signiture" },
          { content: "", href: "/kids" },
          { content: " ", href: "/onepoint" },
        ]}
      />
    </Wrapper>
  );
};

export default Headers;
